<script>
   // shared components - controls
   import AppControlArea from './AppControlArea.svelte';
   import AppControlRange from './AppControlRange.svelte';
   import AppControlSwitch from './AppControlSwitch.svelte';

   import PlotCircle from './AppPlotCircle.svelte';
   import PlotFunction from './AppPlotFunction.svelte';


   const colors = { sin: "#ff0000", cos: "#0000ff"};
   let show = 'both';
   let angle = 60;
   $: angleRad = angle / 180 * Math.PI;
</script>

<div class="app-layout">

      <div class="app-plot1-area">
         <PlotFunction {angle} {colors} {show} />
      </div>

      <div class="app-plot2-area">
         <PlotCircle {angle} {colors} {show} />
      </div>

      <div class="app-controls-area">
         <AppControlArea>
            <AppControlRange label="Angle" bind:value={angle} min={0} max={360} step={1} />
            <AppControlSwitch label="Show" bind:value={show} options={['both', 'sin', 'cos']} />
         </AppControlArea>
         <table>
            <tr>
               <td>angle, degrees</td>
               <td>{angle}</td>
            </tr>
            <tr>
               <td>angle, radians</td>
               <td>{(angleRad).toFixed(5)}</td>
            </tr>
            <tr>
               <td>sin(a)</td>
               <td>{Math.sin(angleRad).toFixed(3)}</td>
            </tr>
            <tr>
               <td>cos(a)</td>
               <td>{Math.cos(angleRad).toFixed(3)}</td>
            </tr>
            <tr>
               <td>sin<sup>2</sup>(a)</td>
               <td>{(Math.sin(angleRad)**2).toFixed(3)}</td>
            </tr>
            <tr>
               <td>cos<sup>2</sup>(a)</td>
               <td>{(Math.cos(angleRad)**2).toFixed(3)}</td>
            </tr>
            <tr>
               <td>sin<sup>2</sup>(a) + cos<sup>2</sup>(a)</td>
               <td>{(Math.sin(angleRad)**2 + Math.cos(angleRad)**2).toFixed(3)}</td>
            </tr>
         </table>
      </div>

</div>

