<script>
   import { Vector } from 'mdatools/arrays';
   import {Axes, XAxis, YAxis, Lines, Box, Points, Segments, TextLabels} from 'svelte-plots-basic/2d';
   import  Legend  from './AppPlotLegend.svelte';

   export let angle;
   export let colors;
   export let show = 'both';

   let x = Vector.seq(-60, 420, 0.1);
   let sinx = x.apply(v => Math.sin(v / 180 * Math.PI));
   let cosx = x.apply(v => Math.cos(v / 180 * Math.PI));

   $: asin = Math.sin(angle / 180 * Math.PI);
   $: acos = Math.cos(angle / 180 * Math.PI);
</script>

<Axes limX={[-60, 420]} limY={[-1.2, 1.5]} yLabel=" " xLabel="Angle, degrees" margins={[1, 1, 0.5, 0.5]}>

   <!-- y = 0 and x = angle lines -->
   <Segments xStart={[-60]} xEnd={[420]} yStart={[0]} yEnd={[0]} lineColor="#00000060" />
   <Segments xStart={[angle]}  xEnd={[angle]} yStart={[-1.2]} yEnd={[1.5]} lineColor="#00000060" lineType={3}/>
   <TextLabels xValues={[angle + 20]} yValues={[1.4]} labels={["angle = " + angle + ""]} pos={2}/>

   {#if show === 'sin' || show === 'both'}
   <Lines xValues={x} yValues={sinx} lineWidth={2} lineColor={colors.sin + '60'}/>
   <Segments xStart={[-60]}  xEnd={[angle]} yStart={[asin]} yEnd={[asin]} lineColor={colors.sin + '60'} lineType={1}/>
   <Points xValues={[angle]} yValues={[asin]} faceColor={colors.sin} borderColor={colors.sin} />
   <TextLabels xValues={[10]} yValues={[asin]} labels={[`sin(${angle}) = ${asin.toFixed(3)}`]} pos={[3]} faceColor={colors.sin}/>
   {/if}

   {#if show === 'cos' || show === 'both'}
   <Lines xValues={x} yValues={cosx} lineWidth={2} lineColor={colors.cos + '60'}/>
   <Segments xStart={[-60]}  xEnd={[angle]} yStart={[acos]} yEnd={[acos]} lineColor={colors.cos + '60'} lineType={1}/>
   <Points xValues={[angle]} yValues={[acos]} faceColor={colors.cos} borderColor={colors.cos} />
   <TextLabels xValues={[10]} yValues={[acos]} labels={[`cos(${angle}) = ${acos.toFixed(3)}`]} pos={[3]} faceColor={colors.cos}/>
   {/if}


   <Legend {show} {colors} />

   <XAxis slot="xaxis" showGrid={true}/>
   <YAxis slot="yaxis" showGrid={true}/>
   <Box slot="box"/>
</Axes>
