<script>
   import { Legend } from 'svelte-plots-basic/2d';

   export let colors;
   export let show = 'both';

   $: lsin = {label:"sin(x)", marker: 2, borderColor: "transparent", faceColor: colors.sin};
   $: lcos = {label:"cos(x)", marker: 2, borderColor: "transparent", faceColor: colors.cos};
   $: legendItems = show === 'both' ? [lsin, lcos] : show === 'sin' ? [lsin] : [lcos];
</script>

{#if legendItems}
<Legend items={legendItems} />
{/if}