<script>
   import { Vector } from 'mdatools/arrays';
   import {Axes, XAxis, YAxis, Lines, Box, Points, Segments, TextLabels} from 'svelte-plots-basic/2d';
   import  Legend  from './AppPlotLegend.svelte';

   export let angle;
   export let colors;
   export let show = 'both';

   let x = Vector.seq(0, 360, 0.1);
   let sinx = x.apply(v => Math.sin(v / 180 * Math.PI));
   let cosx = x.apply(v => Math.cos(v / 180 * Math.PI));

   $: asin = Math.sin(angle / 180 * Math.PI);
   $: acos = Math.cos(angle / 180 * Math.PI);
</script>

<Axes limX={[-1.35, 1.35]} limY={[-1.35, 1.35]} xLabel="cos(x)" yLabel="sin(x)" margins={[0.75, 0.75, 0.5, 0.5]}>

   <!-- the circle and axes -->
   <Segments xStart={[-2]} xEnd={[2]} yStart={[0]} yEnd={[0]} lineColor="#00000030" />
   <Segments yStart={[-2]} yEnd={[2]} xStart={[0]} xEnd={[0]} lineColor="#00000030" />
   <Lines xValues={cosx} yValues={sinx} lineColor="#00000080"/>

   <!-- radius, coordinates and point -->
   <Segments xStart={[0]} yStart={[0]} xEnd={[acos]} yEnd={[asin]} lineColor="#000000"/>
   <Segments xStart={[acos]} yStart={[asin]} xEnd={[acos]} yEnd={[-2]} lineWidth={1} lineType={3} lineColor="#00000080"/>
   <Segments xStart={[acos]} yStart={[asin]} xEnd={[-2]} yEnd={[asin]} lineWidth={1} lineType={3} lineColor="#00000080"/>
   <Points xValues={[acos]} yValues={[asin]} faceColor="#000000" borderColor="#000000" />

   <!-- cos -->
   {#if show === 'cos' || show === 'both'}
   <Segments xStart={[0]} yStart={[0]} xEnd={[acos]} yEnd={[0]} lineWidth={1.5} lineColor={colors.cos}/>
   <TextLabels xValues={[acos/2]} yValues={[0]} labels={[acos.toFixed(3)]} pos={1} faceColor={colors.cos}/>
   {/if}

   <!-- sin -->
   {#if show === 'sin' || show === 'both'}
   <Segments xStart={[acos]} yStart={[0]} xEnd={[acos]} yEnd={[asin]} lineWidth={1.5} lineColor={colors.sin} />
   <TextLabels xValues={[acos + 0.07]} yValues={[asin/2]} labels={[asin.toFixed(3)]} pos={2} faceColor={colors.sin} />
   {/if}

   <Legend {colors} {show} />

   <XAxis slot="xaxis" showGrid={true}/>
   <YAxis slot="yaxis" showGrid={true}/>
   <Box slot="box"/>

</Axes>
